import React, { useState, useEffect } from 'react'

const getTodos = async () => {
	if(await !JSON.parse(localStorage.getItem('agenda-todos'))){
		localStorage.setItem('agenda-todos', JSON.stringify([]))
	}
	const localTodos = await JSON.parse(localStorage.getItem('agenda-todos'))
	return localTodos 
}

const Todo = () => {

	const [inputTask, setInputTask] = useState("")
	const [inputProjet, setInputProjetc] = useState("")
	const [todos, setTodos] = useState([])
	const [onProject, setOnProject] = useState([])
	const [task, setTask] = useState([])
	const [onEdit, setOnEdit] = useState(false)
	const [isloading, setIsLoading] = useState(true)

	const addTask = (e) => {
		e.preventDefault()
		if(onProject.length > 0){
			if(inputTask === ""){
				console.log('input empty')
			}else {
				let i = todos.indexOf(onProject)
				const newtask = {
					date: new Date(),
					title: inputTask,
					completed: false,
				}
				let newTasks = [...onProject[1]]
				newTasks.unshift(newtask)
				let newOnProject = [onProject[0], newTasks]
				let newtodos = [...todos]
				newtodos[todos.indexOf(onProject)] = newOnProject
				setOnProject(newtodos[i])
				setTodos(newtodos)
				saveLocal(newtodos)
				setInputTask("")
			}	
		}else {
			console.log('no project selected')
		}
		
	}

	const saveLocal = async (item) => {
		localStorage.setItem('agenda-todos', JSON.stringify(item))
	}

	const deleteItem = (id) => {
		let newTodos = todos.filter(item => item.date !== id)
		saveLocal(newTodos)
		setTodos(newTodos)
	}

	const editItem = (item) => {
		setInputTask(item.todo)
		setOnEdit(true)
	}

	const addProject = (e) => {
		e.preventDefault()
		if(inputProjet === ""){
			console.log('input project empty')
		}
		else {
			let newProject = [
				{
					title: inputProjet,
					date: new Date()
				},
				[]
			]
			const newTodos = [...todos]
			newTodos.unshift(newProject)
			setTodos(newTodos)
			saveLocal(newTodos)
			setInputProjetc("")
		}
	}

	const changeProject = (id) => {
		 setOnProject(todos[todos.indexOf(id)])
	}

	const fetchTodos = async () => {
		setTodos(await getTodos())
		setIsLoading(false)
	}

	useEffect(() => {
		fetchTodos()
		if(todos.length >= 1){	
			setOnProject(todos[0])
			setTask(todos[onProject].task)
		}else if(todos.length == 0){
			setTask([])
		}
	}, [])

	return (
		<div className="container">
			<div className="row">
				{
					isloading?
					<h4>LOADING</h4>
					:<div className='col-md-3 col-sm-3'>
						<div className="">
							<h5>Projects:</h5>
							<form className="d-flex" >
								<input 
									className="form-control mr-1" 
									type="text" 
									placeholder="Add a new Project" 
									value={inputProjet}
									onChange={(e) => setInputProjetc(e.target.value)}
								/>
								<button 
									className="btn btn-primary btn-sm"
									onClick={(e) => addProject(e)}
								>+</button>
							</form>
						</div>
						<hr/>
						<ul>
						{
							todos.map(project => (
								<li 
									key={project[0].date}
									onClick={() => changeProject(project)}
									className={project === onProject?"isSelected":""}
								>
									{project[0].title}
								</li>
							))		
						}
						</ul>
					</div>
				}
				
				<div className="col-md-9 col-sm-9">
					<div className="d-flex justify-content-between">
						<h5>Tasks:</h5>
						<form className="d-flex">
							<input 
								className="form-control mr-1" 
								type="text" 
								placeholder="Add a new Task" 
								value={inputTask}
								onChange={(e) => setInputTask(e.target.value)}
							/>
							<button 
								className="btn btn-primary btn-sm" 
								type="submit"
								onClick={(e) => addTask(e)}
							>
								+
							</button>
						</form>
					</div>
					<hr/>
					<div className="card m-2 border-0">
					{
						onProject.length > 0?
							onProject[1].length > 0?
							onProject[1].map(task => (
								<div className="card-content d-flex justify-content-between align-items-center" key={task.date}>
									<h6 className="m-1">{task.title}</h6>
									<div>									
										<button className="btn btn-success btn-sm"  >Terminado</button>
										<button className="btn btn-primary btn-sm mx-1" >delete</button>
										<button className="btn btn-primary btn-sm" >edit</button>
									</div>
								</div>	
							))
							:<h4>No Task</h4>
						:<h4>No Project Selected</h4>
					}
					
						
						<hr/>
					</div>	
				</div>	
			</div>
		</div>
	)
}

export default Todo